
html,
body,
#app {
  width: 100%;
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body,
dd,
dl,
dt,
fieldset,
form,
h1,
h2,
h3,
h4,
h5,
h6,
html,
img,
legend,
li,
ol,
p,
ul {
  margin: 0;
  padding: 0;
  -moz-user-select: none;
  -webkit-user-select: none;
  -webkit-tap-highlight-color: transparent;
}
button,
fieldset,
img,
input {
  border: none;
  padding: 0;
  margin: 0;
  outline-style: none;
}
ol,
ul {
  list-style: none;
}
input {
  padding-top: 0;
  padding-bottom: 0;
  font-family: "SimSun", "宋体";
}
input,
select {
  vertical-align: middle;
}
input,
select,
textarea {
  font-size: 12px;
  margin: 0;
}
textarea {
  resize: none;
}
/*防止拖动*/
img {
  border: 0;
  vertical-align: middle;
}
/*  去掉图片低测默认的3像素空白缝隙*/
table {
  border-collapse: collapse;
}
i {
  font-style: normal;
}
body {
  font-family: PingFangSC-Light, helvetica, 'Heiti SC';
  color: #666;
  background: #fff;
}
a {
  color: #666;
  text-decoration: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-decoration: none;
  font-weight: normal;
  font-size: 100%;
  font-family: PingFangSC-Light, helvetica, 'Heiti SC';
}
.ac {
  text-align: center;
}
.pull-left {
  float: left;
  display: inline-block;
}
.pull-right {
  float: right;
  display: inline-block;
}
.clearfix::after {
  display: block;
  content: "";
  clear: both;
}
.aside .lnb .nav-wrap .scroll > ul > li > ul li.active > a:not(:last-child) {
  background: #364453;
}
.layout-leftright .aside {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  font-weight: bold;
  background: #2d2f3e;
  color: #fff;
  width: 240px;
  z-index: 1002;
}
.layout-leftright .aside .lnb .logo {
  height: 60px;
  background: #FFAA00;
}
.layout-leftright .aside .lnb ul {
  padding-top: 12px;
}
.layout-leftright .aside .lnb ul li {
  height: 28px;
  line-height: 28px;
}
.layout-leftright .aside .lnb ul li a {
  width: 100%;
  display: block;
  color: #fff;
  font-size: 12px;
  font-weight: 500;
  padding-left: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.layout-leftright .aside .lnb ul li a:hover,
.layout-leftright .aside .lnb ul li a.active {
  color: #FFAA00;
}
.layout-leftright .aside .lnb ul li a:hover.active,
.layout-leftright .aside .lnb ul li a.active.active {
  background: #364453;
  color: #FFAA00;
}
.layout-leftright .aside .lnb ul li ul {
  display: none;
}
.layout-leftright .aside .lnb ul li.active {
  background: #2f3c49;
  height: auto;
}
.layout-leftright .aside .lnb ul li.active :first-child {
  background: #364453;
  color: #FFAA00;
}
.layout-leftright .aside .lnb ul li.active ul {
  display: block;
}
.layout-leftright .aside .lnb ul li.active ul li a {
  background: #2f3c49 !important;
  color: #fff !important;
  padding-left: 46px;
}
.layout-leftright .aside .lnb ul li.active ul li.active a {
  background: #364453 !important;
  color: #FFAA00 !important;
}
.layout-leftright .layout-right {
  position: relative;
  color: #222;
  min-width: 920px;
  margin-left: 240px;
}
.app-header {
  position: relative;
}
.app-header .breadcrumb {
  height: 60px;
  line-height: 60px;
  padding-left: 20px;
}
.app-header .breadcrumb ul {
  font-size: 0px;
}
.app-header .breadcrumb ul li {
  font-size: 12px;
}
.app-header .breadcrumb ul li:hover a {
  color: #FFAA00;
}
.app-header .breadcrumb ul li.active a {
  color: #FFAA00;
}



